import React from 'react';
import './PageNotFound.css';

const PageNotFound = () => {
  return (
    <div id="pageNotFound">
      <h1>404</h1>
      <p>Page Not Found</p>
      <p>Return to <a href="/">mlech413.github.io</a></p>
    </div>
  );
}

export default PageNotFound;


